body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto Condensed', sans-serif;
  overflow-x: hidden;
  min-width: 320px;
}

body img {
  max-width: 100%;
  height: auto;
}

body a {
  text-decoration: none;
}

body h1 {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  text-transform: uppercase;
  font-size: 37px;
}

body h2,
body h3,
body h4,
body h5,
body h6 {
  margin: 0;
}

body p {
  margin: 0;
}

body .wrapper {
  box-sizing: border-box;
  height: 100vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

body .wrapper .box {
  flex-basis: 635px;
  margin-right: 20px;
}

body .wrapper .box:last-child {
  margin-right: 0;
}

body .wrapper .box .title-box {
  display: flex;
  justify-content: space-between;
}

body .wrapper .box .title-box .tabs {
  display: flex;
}

body .wrapper .box .title-box .tabs .tab {
  display: flex;
  align-items: flex-end;
}

body .wrapper .box .title-box .tabs .tab a.link {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  color: #000;
  background: #edecee;
  padding: 7px 15px;
}

body .wrapper .box .title-box .tabs .tab a.link#tab-active-all {
  background: #4AD92E;
  color: #fff;
}

body .wrapper .box .title-box .tabs .tab a.link#tab-active-web {
  background: #ea00d9;
  color: #fff;
}

body .wrapper .box .title-box .tabs .tab a.link#tab-active-des {
  background: #81B1EC;
  color: #fff;
}

body .wrapper .box .title-box .tabs .tab a span.website {
  background: #D700C8;
  display: inline-block;
  height: 10px;
  width: 10px;
  border-radius: 50%;
}

body .wrapper .box .title-box .tabs .tab a span.design {
  display: inline-block;
  background: #81B1EC;
  height: 10px;
  width: 10px;
  border-radius: 50%;
}

body .wrapper .box .title-box .tabs .tab a span.small {
  display: none;
}

body .wrapper .box .title-box .tabs .tab a span.big {
  display: block;
}

body .wrapper .box .title-box .tabs .tab a span.active-tab {
  background: #fff !important;
}

body .wrapper .box .content {
  height: 450px;
  box-sizing: border-box;
  position: relative;
}

body .wrapper .box .content.portfolio {
  padding: 8px;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  overflow-X: hidden;
}

body .wrapper .box .content.portfolio .not-found {
  margin: auto;
  text-align: center;
  display: none;
}

body .wrapper .box .content.portfolio .not-found .fab {
  font-size: 50px;
  margin-bottom: 10px;
  color: #878788;
}

body .wrapper .box .content.portfolio .not-found h4 {
  font-size: 20px;
  margin-bottom: 5px;
}

body .wrapper .box .content.portfolio .item {
  display: flex;
  width: 100%;
  height: 85px;
  background: #fff;
  margin-bottom: 4px;
  -webkit-box-shadow: 0px 2px 3px -1px rgba(50, 50, 50, 0.65);
  -moz-box-shadow: 0px 2px 3px -1px rgba(50, 50, 50, 0.65);
  box-shadow: 0px 2px 3px -1px rgba(50, 50, 50, 0.65);
}

body .wrapper .box .content.portfolio .item.web .label {
  background: #ea00d9;
  height: 100%;
  width: 10px;
}

body .wrapper .box .content.portfolio .item.web .circle {
  height: 10px;
  width: 10px;
  background: #ea00d9;
  margin-right: 4px;
  border-radius: 50%;
}

body .wrapper .box .content.portfolio .item.des .label {
  background: #81B1EC;
  height: 100%;
  width: 10px;
}

body .wrapper .box .content.portfolio .item.des .circle {
  height: 10px;
  width: 10px;
  background: #81B1EC;
  margin-right: 4px;
  border-radius: 50%;
}

body .wrapper .box .content.portfolio .item .item-body {
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

body .wrapper .box .content.portfolio .item .item-body .title {
  width: 100%;
  padding-right: 10px;
}

body .wrapper .box .content.portfolio .item .item-body .title .name {
  display: flex;
  flex-wrap: wrap;
}

body .wrapper .box .content.portfolio .item .item-body .title .name h3 {
  text-transform: uppercase;
}

body .wrapper .box .content.portfolio .item .item-body .title .name .point {
  display: flex;
  align-items: center;
  margin-left: 10px;
}

body .wrapper .box .content.portfolio .item .item-body .title .name .point a {
  color: #9194A1;
  text-transform: uppercase;
  font-size: 13px;
  line-height: 13px;
  border-bottom: 1px solid #EAEAEA;
}

body .wrapper .box .content.portfolio .item .item-body .title p {
  font-size: 15px;
  color: #495057;
}

body .wrapper .box .content.portfolio .item .item-body .buttons {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-around;
}

body .wrapper .box .content.portfolio .item .item-body .buttons a {
  color: #fff;
  font-weight: 500;
  padding: 2px 30px;
  background: #4AD92E;
  border-top: 2px solid #3FB827;
}

body .wrapper .box .content.portfolio .item .information {
  position: absolute;
  background: #EDECEE;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transform: translateX(-620px);
  display: none;
  transition: transform .6s;
  padding: 10px;
  box-sizing: border-box;
}

body .wrapper .box .content.portfolio .item .information .title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #E0DFE1;
}

body .wrapper .box .content.portfolio .item .information .title .name {
  padding: 6px;
  width: 100%;
}

body .wrapper .box .content.portfolio .item .information .title .name h3 {
  color: #878190;
  text-transform: capitalize;
  font-size: 20px;
}

body .wrapper .box .content.portfolio .item .information .title .back .exit .fas {
  padding: 8px;
  background: #4AD92E;
  font-size: 22px;
  color: #fff;
  transition: background .5s;
}

body .wrapper .box .content.me {
  padding: 0 8px;
}

body .wrapper .box .content.me .name {
  margin-bottom: 5px;
}

body .wrapper .box .content.me .name h2 {
  font-size: 40px;
}

body .wrapper .box .content.me .about h3 {
  font-size: 19px;
  margin-bottom: 10px;
  color: #363b41;
}

body .wrapper .box .content.me .about h4 {
  font-size: 25px;
}

body .wrapper .box .content.me .about .progress {
  background: #EDECEE;
  width: 100%;
  height: 3px;
}

body .wrapper .box .content.me .about .progress .p-html-css {
  box-shadow: 0px 0px 10px #FF6E00;
  overflow: hidden;
  height: 100%;
  width: 0%;
  background: #FF6E00;
}

body .wrapper .box .content.me .about .progress .p-js-jquery {
  box-shadow: 0px 0px 10px #F7B517;
  overflow: hidden;
  height: 100%;
  width: 0%;
  background: #F7B517;
}

body .wrapper .box .content.me .about .progress .p-adobe {
  box-shadow: 0px 0px 10px #D12B0D;
  overflow: hidden;
  height: 100%;
  width: 0%;
  background: #D12B0D;
}

body .wrapper .box .content.me .about .skills {
  margin-top: 15px;
  max-width: 410px;
}

body .wrapper .box .content.me .about .skills .skill {
  margin-bottom: 15px;
}

body .wrapper .box .content.me .about .skills .skill:last-child {
  margin-bottom: 10px;
}

body .wrapper .box .content.me .about .skills .skill .skill-name {
  margin-bottom: 5px;
  display: flex;
  justify-content: space-between;
}

body .wrapper .box .content.me .about .skills .skill .skill-name p {
  text-transform: uppercase;
}

body .wrapper .box .content.me .about .p-bootstrap {
  text-transform: uppercase;
  color: #733FC0;
}

body .wrapper .box .content.me .about .p-less {
  text-transform: uppercase;
  color: #1B3557;
}

@media (max-width: 1290px) {
  body .wrapper .box {
    flex-basis: 600px;
  }
}

@media (max-width: 1220px) {
  body .wrapper .box {
    flex-basis: 580px;
  }
}

@media (max-width: 1180px) {
  body .wrapper .box {
    flex-basis: 550px;
  }
}

@media (max-width: 1119px) {
  body {
    padding-top: 30px;
  }

  body .wrapper .box.first-box {
    order: 1;
    margin-top: 30px;
    padding-bottom: 100px;
  }

  body .wrapper .box {
    margin-right: 0;
    flex-basis: 635px;
  }

  body .wrapper .box .content.me {
    height: 550px;
  }

  body .wrapper .box .content.me .name h2 {
    text-align: center;
  }

  body .wrapper .box .content.me .about h3 {
    text-align: center;
    font-size: 17px;
    margin-bottom: 30px;
  }

  body .wrapper .box .content.me .about .skills {
    max-width: 100%;
  }
}

@media (max-width: 465px) {
  body h1 {
    font-size: 35px;
  }
}

@media (max-width: 440px) {
  body .wrapper .box .title-box .tabs .tab a span.small {
    display: inline-block;
  }

  body .wrapper .box .title-box .tabs .tab a span.big {
    display: none;
  }
}

@media (max-width: 360px) {
  body .wrapper .box .content.portfolio .item .item-body .buttons a {
    padding: 2px 16px;
  }
}

@media (max-width: 345px) {
  body h1 {
    font-size: 30px;
  }
}